/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "AvenirNextCyr", sans-serif;
  font-style: normal;
  font-weight: 300;
  src: local("AvenirNextCyr-Light"), local("AvenirNextCyr-Light"), url("../fonts/AvenirNextCyr-Light.woff") format("woff");
}

@font-face {
  font-family: "AvenirNextCyr", sans-serif;
  font-style: normal;
  font-weight: 400;
  src: local("AvenirNextCyr-Regular"), local("AvenirNextCyr-Regular"), url("../fonts/AvenirNextCyr-Regular.woff") format("woff");
}

@font-face {
  font-family: "AvenirNextCyr", sans-serif;
  font-style: normal;
  font-weight: 500;
  src: local("AvenirNextCyr-Medi"), local("AvenirNextCyr-Medi"), url("../fonts/AvenirNextCyr-Medi.woff") format("woff");
}

@font-face {
  font-family: "AvenirNextCyr", sans-serif;
  font-style: normal;
  font-weight: 700;
  src: local("AvenirNextCyr-Bold"), local("AvenirNextCyr-Bold"), url("../fonts/AvenirNextCyr-Bold.woff") format("woff");
}

.icon-arrow-left {
  width: 0.58em;
  height: 1em;
  fill: currentColor;
}

.icon-arrow-right {
  width: 0.58em;
  height: 1em;
  fill: currentColor;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #FFF;
  font-family: "AvenirNextCyr", sans-serif;
  font-size: 18px;
  line-height: 1.33;
}

@media only screen and (max-width: 767px) {
  body {
    font-size: 16px;
  }
}

.text-decorated {
  display: inline-block;
  position: relative;
  background: linear-gradient(to top, transparent, transparent 0.1em, #61AB91 0.1em, #61AB91 0.3em, transparent 0.3em, transparent);
}

.text-medium {
  font-weight: 500;
}

.text-bold {
  font-weight: 700;
}

.board-title {
  font-size: 48px;
  font-weight: 300;
  line-height: 1;
}

.text-green {
  color: #61AB91;
}

.inner {
  max-width: 1150px;
  padding: 0 20px;
  width: 100%;
  margin: 0 auto;
}

.block {
  display: flex;
}

.header {
  padding: 28px 0;
}

.logo-image {
  height: 40px;
}

.board-section {
  background-color: #F8F8F2;
  padding: 55px 0;
}

.board-grid {
  display: flex;
  flex-wrap: wrap;
}

.board-grid__item {
  flex: 0 0 12.5%;
  min-width: 12.5%;
  padding-bottom: 12.5%;
  position: relative;
  box-shadow: 0 0 0 1px #DEDEDE inset;
}

@media only screen and (max-width: 767px) {
  .board-grid__item {
    flex: 0 0 33.333%;
    min-width: 33.333%;
    padding-bottom: 33.333%;
  }
}

.board-grid__item .card {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.board-grid__item--first {
  flex: 0 0 25%;
  min-width: 25%;
  box-shadow: none;
}

@media only screen and (max-width: 767px) {
  .board-grid__item--first {
    flex: 0 0 100%;
    min-width: 100%;
  }
}

.board-grid__item--first .board-title {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.card {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-color: rgba(196, 196, 196, 0.5);
  background-blend-mode: multiply;
}

.card__content {
  background-color: #61AB91;
  padding: 10px;
  color: #fff;
  font-weight: 400;
}

.card__name {
  font-size: 14px;
}

.card__summ {
  font-size: 9px;
}

.slider {
  padding: 0 72px;
}

@media only screen and (max-width: 767px) {
  .slider {
    padding: 0;
    margin: 0 -20px;
  }
}

.slider__slide {
  height: 300px;
  position: relative;
}

.slider__slide iframe {
  height: 100%;
}

.slider__slide .slider__pause {
  display: none;
}

.slider__slide .slider__play {
  display: block;
}

.slider__slide.is-playing .slider__pause {
  display: block;
  opacity: 0;
}

.slider__slide.is-playing .slider__play {
  display: none;
}

.no-touch .slider__slide.is-playing:hover .slider__pause {
  opacity: 1;
}

.slider__next, .slider__prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slider__next {
  right: 36px;
}

.slider__prev {
  left: 36px;
}

.slider__video-buttons {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slider__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider__pause {
  display: none;
}

.slider-wrapper {
  position: relative;
}

.slider-btn {
  color: #61AB91;
  background-color: transparent;
}

.slider-btn .icon {
  font-size: 20px;
}

.slick-slide iframe {
  pointer-events: none;
}

.video-btn {
  display: inline-block;
  height: 110px;
  background-color: transparent;
}

.video-btn svg {
  height: 100%;
}

.slick-dots {
  display: flex;
  justify-content: center;
  padding: 28px 0;
}

.slick-dots li {
  display: block;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  border: 1px solid #61AB91;
}

.descr-section__top {
  padding: 68px 0;
}

.descr-section__top p {
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}

.descr-section__image {
  margin-bottom: 72px;
}

@media only screen and (max-width: 767px) {
  .descr-section__image {
    margin-left: -20px;
    margin-right: -20px;
  }
}

.descr-section__image img {
  display: block;
  max-width: 100%;
}

.descr-section__bottom {
  margin-bottom: 50px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.descr-section__bottom-block {
  flex: 0 0 calc(50% - 65px);
  min-width: calc(50% - 65px);
}

@media only screen and (max-width: 767px) {
  .descr-section__bottom-block {
    flex: 0 0 100%;
    min-width: 100%;
  }
}

.descr-section__bottom-block p:not(:last-child) {
  margin-bottom: 1.25em;
}

.about-section__title {
  margin-bottom: 86px;
  text-align: center;
}

.about-section__title h2 {
  font-size: 50px;
  line-height: 1.1;
}

.about-section__slider {
  margin-bottom: 200px;
}
