.descr-section {
  &__top {
    padding: 68px 0;
    p {
      max-width: 900px;
      margin: 0 auto;
      text-align: center; } }
  &__image {
    margin-bottom: 72px;
    @include sm {
      margin-left: -20px;
      margin-right: -20px; }
    img {
      display: block;
      max-width: 100%; } }
  &__bottom {
    margin-bottom: 50px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  &__bottom-block {
    @include fsize(calc(50% - 65px));

    @include sm {
      @include fsize(100%); }
    p {
      &:not(:last-child) {
        margin-bottom: 1.25em; } }
