.slider {
  padding: 0 72px;
  @include sm {
    padding: 0;
    margin: 0 -20px; }
  &__slide {
    height: 300px;
    position: relative;
    iframe {
      height: 100%; }
    .slider__pause {
      display: none; }
    .slider__play {
      display: block; }
    &.is-playing {
      .slider__pause {
        display: block;
        opacity: 0; }
      .slider__play {
        display: none; }
      @include hover {
        .slider__pause {
          opacity: 1; } } } }
  &__next,
  &__prev {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); }
  &__next {
    right: 36px; }
  &__prev {
    &__next {}
    left: 36px; }
  &__video-buttons {
    @include vcenter; }
  &__video {
    @include coverdiv;
    object-fit: cover; }
  &__pause {
    display: none; } }

.slider-wrapper {
  position: relative; }

