.slider-btn {
  color: $green;
  background-color: transparent;
  .icon {
    font-size: 20px; } }

.slick-slide iframe {
  pointer-events: none; }

.video-btn {
  display: inline-block;
  height: 110px;
  background-color: transparent;
  svg {
    height: 100%; } }

.slick-dots {
  display: flex;
  justify-content: center;
  padding: 28px 0;
  li {
    display: block;
    height: 10px;
    width: 10px;
    border-radius: 50%;
    border: 1px solid $green; } }
